@include title-sizes();

.text-block {
	p:last-child {
		margin-bottom: 0;
	}
}

h1, .h1 {
	margin-bottom: 20px;

	&.text-lettering-animation {
		line-height: 0.6;

		.word {
			line-height: 1.2;
		}
	}
}

h2, .h2 {
	line-height: 1.2;
	margin-bottom: 10px;
}

h3, .h3 {
	line-height: 1.2;
	margin-bottom: 25px;
}

h4, .h4 {

}

h5, .h5 {
	line-height: 1.33;
	margin-bottom: 15px;

	@include min-w(md) {
		margin-bottom: 20px;
	}
}

h6, .h6 {
	margin-bottom: 15px;
}

.address-text {
	font-size: 19px;
	font-style: normal;
	color: $gray-500;
	line-height: 1.6;
	margin-bottom: 0;
}

.list-with-dots {
	@include listreset;

	font-size: 18px;
	line-height: 1.739;
	font-weight: 700;

	@include min-w(md) {
		font-size: 20px;
	}

	@include min-w(lg) {
		font-size: 23px;
	}

	li {
		position: relative;
		padding-left: 15px;

		&:not(:last-child) {
			margin-bottom: 2px;
		}

		&:before {
			content: '•';
			display: inline-block;
			position: absolute;
			top: 9px;
			left: 0;
			font-size: 10px;
			line-height: 1;

			@include min-w(md) {
				top: 12px;
			}

			@include min-w(lg) {
				top: 13px;
			}
		}
	}
}

.list-with-squares {
	@include listreset;

	font-size: 18px;
	line-height: 1.6136;

	@include min-w(md) {
		font-size: 20px;
	}

	@include min-w(lg) {
		font-size: 22px;
	}

	li {
		position: relative;
		padding-left: 24px;

		&:before {
			content: '';
			position: absolute;
			top: 11px;
			left: 0;
			display: block;
			width: 8px;
			height: 8px;
			background-color: $white;
			border-radius: 2px;

			@include min-w(md) {
				width: 11px;
				height: 11px;
				top: 11px;
			}

			@include min-w(lg) {
				top: 13px;
			}
		}
	}
}

.section-default {
	ul {
		margin: 20px 0;
		list-style-type: disc;
		list-style-position: inside;
	}

	ol {
		margin: 20px 0;
		padding-left: 0;
		list-style-position: inside;
	}

	blockquote {
		margin: 40px 0;
		padding-left: 50px;
		border-left: 2px solid $primary;

		p {
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	a {
		&:not([class]) {
			transition: color 0.3s;
		}
	}

	del {
		text-decoration: line-through;
	}

	hr {
		margin: 30px 0;
		border-color: rgba($black, 0.2);
	}
}