.section {
	padding: 40px 0;

	@include min-w(md) {
		padding: 80px 0;
	}

	@include min-w(lg) {
		padding: 120px 0;
	}

	&-welcome {
		@include bgCover;

		padding: 130px 0;

		@include min-w(sm) {
			padding: 130px 0 150px;
		}

		@include min-w(md) {
			padding: 150px 0 200px;
		}

		@include min-w(lg) {
			padding: 180px 0 220px;
		}

		@include min-w(xl) {
			padding: 215px 0 355px;
		}

		&__main-content {
			max-width: 750px;
		}
	}

	&-selected-text {
		@include bgCover;

		@include min-w(lg) {
			padding: 90px 0;
		}

		@include min-w(xl) {
			padding: 105px 0 115px;
		}

		&__text {
			text-align: center;
			max-width: 920px;
			margin: 0 auto;
			font-size: 18px;
			line-height: 1.5;

			@include min-w(md) {
				font-size: 20px;
			}

			@include min-w(lg) {
				font-size: 23px;
			}

			p {
				margin-bottom: 35px;
			}
		}
	}

	&-about-us {
		@include bgCover;

		color: $white;

		@include min-w(lg) {
			padding: 110px 0;
		}

		&__main-content {
			max-width: 755px;
		}

		&__description {
			margin-bottom: 30px;
		}
	}

	&-our-services {
		@include bgCover;

		padding-bottom: 250px;

		@include min-w(lg) {
			padding-top: 105px;
			padding-bottom: 295px;
		}

		+ .section-industries {
			padding: 0 0 40px 0;
			margin-top: -160px;

			@include min-w(md) {
				padding: 0 0 90px 0;
			}
		}

		&__main-content {
			@include min-w(lg) {
				max-width: 465px;
			}
		}

		&__description {
			margin-bottom: 35px;
		}

		&__list {
			margin: 50px 0 0 0;

			@include min-w(lg) {
				margin: 95px 0 0 15px;
				max-width: 485px;
			}
		}
	}

	&-industries {
		&__block {
			background-color: $white;
			border-radius: 8px;
			overflow: hidden;

			@include min-w(lg) {
				display: flex;
				flex-wrap: wrap;
			}
		}

		&__title-block {
			padding: 15px;

			@include min-w(sm) {
				padding: 25px;
			}

			@include min-w(md) {
				padding: 40px;
			}

			@include min-w(lg) {
				flex: 0 0 34%;
				max-width: 34%;
				text-align: right;
				padding: 85px 40px;
			}

			@include min-w(xl) {
				padding: 110px 70px;
			}
		}

		&__content-block {
			@include bgCover;

			padding: 15px;
			color: $white;

			@include min-w(sm) {
				padding: 25px;
			}

			@include min-w(md) {
				padding: 60px 40px;
			}

			@include min-w(lg) {
				flex: 0 0 66%;
				max-width: 66%;
				padding: 85px 40px;
			}

			@include min-w(xl) {
				padding: 110px 70px;
			}
		}

		&__list {
			@include min-w(md) {
				column-count: 2;
			}
		}
	}

	&-welcome-small {
		@include bgCover;

		text-align: center;
		padding: 135px 0 75px;

		@include min-w(lg) {
			padding-top: 155px;
		}
	}

	&-phases-of-our-process {
		&__list {
			.section-phases-of-our-process__item {
				&:nth-child(2n) {
					.section-phases-of-our-process__item-content {
						@include min-w(md) {
							margin-left: auto;
						}
					}
				}
			}
		}

		&__item {
			@include bgCover;

			color: $white;

			@include min-w(xl) {
				padding: 150px 0;
			}
		}

		&__item-content {
			max-width: 530px;
		}
	}

	&-contacts {
		@include min-w(xl) {
			padding: 130px 0 125px;
		}

		&__main-content {
			max-width: 380px;
		}

		&__address {
			margin-bottom: 30px;
		}

		&__form-block {
			padding-top: 60px;

			@include min-w(md) {
				padding-top: 80px;
			}
		}
	}
}