textarea {
	&.form-control {
		height: 175px;
	}
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

input[type=number] {
	-moz-appearance: textfield;
}

form {
	button {
		&.btn {
			margin-top: 10px;

			@include min-w(xl) {
				min-width: 340px;
			}
		}
	}
}

.form-control {
	@include max-w(lg) {
		padding: 1rem;
		height: add($input-line-height * 1em, add(1rem * 2, $input-height-border, false));
		font-size: 16px;
	}
}

// CF7

.wpcf7 {
	.wpcf7-not-valid-tip {
		color: $danger;
		font-size: 16px;
		padding-top: 5px;
	}

	form .wpcf7-response-output {
		margin: 30px 0 0 0;
		padding: 10px;
		font-size: 16px;
	}

	form.invalid .wpcf7-response-output,
	form.unaccepted .wpcf7-response-output {
		border-color: $warning;
	}

	form.sent .wpcf7-response-output {
		border-color: $success;
	}
}

// CF7 END